import React from 'react'
import avatar from './profile.png';
import './profile.css'

export default class Profile extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      name: this.props.user.name,
      age: this.props.user.age,
      hobby: this.props.user.hobby
    }
  }

  onProfileUpdate = (data) => {
    fetch(`https://vision-ai-api.vercel.app/profile/${this.props.user.id}`, {
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        formInput: data
      })
    }).then(resp => {
      if (resp.status === 200 || resp.status === 304) {
        this.props.toggleModal();
        this.props.LoadUser({ ...this.props.user, ...data });
      }
    }).catch(console.log)
  }

  onFormChange = (event) => {
    switch(event.target.name) {
      case 'user-name':
        this.setState({name: event.target.value})
        break;
      case 'user-age':
        this.setState({age: event.target.value})
        break;
      case 'user-hobby':
        this.setState({hobby: event.target.value})
        break;
      default:
        return;
    }
  }
  render() {
    const { toggleModal, user } = this.props;
    const { name, age, hobby } = this.state;
    return (
      <div className='profile-modal'>
        <article className='center br3 ba b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 bg-white'>
          <main className='pa4 w-80 m-auto'>
            <img
              src={avatar}
              className='h3 w3 dib' alt='avatar'
            />
             <h1>{user.name}</h1>
            <h4>{`Images submitted: ${user.count}`}</h4>
            <h5> {`Member since: ${new Date(user.joined).toLocaleDateString()}`}</h5>

            <hr />
            <label className='mt2 fw6' htmlFor='user-name'>Name:</label>
            <input onChange={this.onFormChange} type='text' name='user-name' className='pa2 ba w-100' placeholder={name}></input>
            <label className='mt2 fw6' htmlFor='user-age'>Age:</label>
            <input onChange={this.onFormChange} type='text' name='user-age' className='pa2 ba w-100' placeholder={age}></input>
            <label className='mt2 fw6' htmlFor='user-hobby'>Favourite hobby:</label>
            <input onChange={this.onFormChange} type='text' name='user-hobby' className='pa2 ba w-100' placeholder={hobby}></input>
            <div className='mt4' style={{ display: 'flex', justifyContent: 'space-evenly'}}>
              <button className='b pa2 grow pointer hover-white w-40 bg-light-blue b--black-20'
                onClick={() => this.onProfileUpdate({name, age, hobby})}>
                Save
              </button>
              <button className='b pa2 grow pointer hover-white w-40 bg-light-red b--black-20'
                onClick={toggleModal}>
                Cancel
              </button>
            </div>
          </main>
        </article>
      </div>
    );
  }
}
